import axios from 'axios';

const urlget = 'https://sliitconferenceweb.herokuapp.com/conference/readapproved';
const urlupdate = 'https://sliitconferenceweb.herokuapp.com/conference/Editorupdate';
const urlgetEditor = 'https://sliitconferenceweb.herokuapp.com/conference/readEditor';
const urlgetadmin = 'https://sliitconferenceweb.herokuapp.com/conference/readadmin';
const urlupdateApprovel = 'https://sliitconferenceweb.herokuapp.com/conference/adminupdate';
const urlconferenceDecline = 'https://sliitconferenceweb.herokuapp.com/conference/conferenceDecline';
const urlconferenceApprove = 'https://sliitconferenceweb.herokuapp.com/conference/conferenceApprove';

export const fetchEvents =()=>axios.get(urlget);
export const updateconference = (id,updateEvent)=>axios.patch(`${urlupdate}/${id}`,updateEvent);
export const fetchEditorConference =()=>axios.get(urlgetEditor);
export const fetchAdminConference =()=>axios.get(urlgetadmin);
export const updateApprovel = (id,updateEvent)=>axios.patch(`${urlupdateApprovel}/${id}`,updateEvent);
export const conferenceDecline = (id,updateEvent)=>axios.patch(`${urlconferenceDecline}/${id}`,updateEvent);
export const conferenceApprove = (id,updateEvent)=>axios.patch(`${urlconferenceApprove}/${id}`,updateEvent);


