import React, { useEffect, useState } from "react";
import axios from "axios";
import ReactDOM from 'react-dom';
import AppBarComponent from "../LandingPage/AppBar/AppBarComponent";
import AOS from 'aos';
import '../../../node_modules/aos/dist/aos.css'; 
import Footer from "../LandingPage/Footer/Footer";

AOS.init();
export default function PaperPaidList() {
    const [userType, seTuserType] = useState('')
    const [PaperPaids, setPaperPaids] = useState([]);

    useEffect(() => {
        const getusertype = async () => {
            const access_token = localStorage.getItem('token')
            console.log(access_token)
            let config = {
                headers: {
                    'Authorization': 'Bearer ' + access_token
                }
            }
            axios.get('https://sliitconferenceweb.herokuapp.com/user/post', config).then((response) => {
                if (response.data.message) {
                    alert(response.data.message)
                } else {
                    seTuserType(response.data.user.usertype)

                }
            })
                .catch()
        };
        getusertype();


        function getList() {
            axios.get("https://sliitconferenceweb.herokuapp.com/payment/allpayments").then((res) => {
                //console.log(res)
                setPaperPaids(res.data.data)
            }).catch((err) => {
                alert(err.message)
            })
        }

        getList();

    })


    return (

        <div>
            <AppBarComponent getUserType={userType} />
            <div className="container my-5">
                <div className="dialog-title" style={{marginBottom:"10%"}}>
                    List Research Paper paids
                    <hr className="workshopStyle" />
                </div>
                <div style={{display:"grid", gridTemplateColumns: "450px 450px 450px"}}>
                {
                    PaperPaids.map(PaperPaids => (
                        <div className="d-flex justify-content-center" data-aos="zoom-in-up">
                            <div key={PaperPaids._id} className="confDetails ShadowL" style={{ height: "fit-content" }}>

                                <div className="card-body">
                                    <p className="card-text"><strong>User Name :</strong> {PaperPaids.users.username}</p>
                                    <p className="card-text"><strong>Email :</strong> {PaperPaids.users.email}</p>
                                    <p className="card-text"><strong>Amount :</strong> {PaperPaids.amount}</p>
                                    <p className="card-text"><strong>Type :</strong> {PaperPaids.type}</p>
                                    <p className="card-text"><strong>Date :</strong> {PaperPaids.PaidDate}</p>

                                </div>
                            </div>
                        </div>

                    ))
                }
                </div>
            </div>
   
        </div>
    )
}