import axios from 'axios';

const urlget = 'https://sliitconferenceweb.herokuapp.com/mainevent/readAll';
const urladd = 'https://sliitconferenceweb.herokuapp.com/mainevent/add';
const urlupdate = 'https://sliitconferenceweb.herokuapp.com/mainevent/update';
const urldelete = 'https://sliitconferenceweb.herokuapp.com/mainevent/delete';
const urlFetchNo = 'https://sliitconferenceweb.herokuapp.com/mainevent//readNo';
const urlaprrove = 'https://sliitconferenceweb.herokuapp.com/mainevent/approve';
const urldecline = 'https://sliitconferenceweb.herokuapp.com/mainevent/decline';
const urlFetchApproved = 'https://sliitconferenceweb.herokuapp.com/mainevent//readApproved';
const urlFetchdecline = 'https://sliitconferenceweb.herokuapp.com/mainevent//readDecline';
const urlFetchWorkshopEvents = 'https://sliitconferenceweb.herokuapp.com/mainevent//readworkshopsEvents';
const urlFetchReserchEvents = 'https://sliitconferenceweb.herokuapp.com/mainevent//readresearchEvents';





export const fetchEvents =()=>axios.get(urlget);
export const createEvent =(newEvent)=>axios.post(urladd,newEvent);
export const updateEvent = (id,updateEvent)=>axios.patch(`${urlupdate}/${id}`,updateEvent);
export const deleteEvent = (id)=>axios.delete(`${urldelete}/${id}`);

export const  fetchNoEvents =()=>axios.get(urlFetchNo);
export const updateaprovel = (id,updateEvent)=>axios.patch(`${urlaprrove}/${id}`,updateEvent);
export const updatedecline = (id,updateEvent)=>axios.patch(`${urldecline}/${id}`,updateEvent);

export const  fetchApprovedEvents =()=>axios.get(urlFetchApproved);
export const  fetchDeclineEvents =()=>axios.get(urlFetchdecline);

export const  fetchworkshpsEvents =()=>axios.get(urlFetchWorkshopEvents);
export const  fetchresearchesEvents =()=>axios.get(urlFetchReserchEvents);









